import { React, useState } from "react";
import { Typewriter } from "react-simple-typewriter";
import { invisibleValues, motion } from "framer-motion";

import Images from "../../../../assets/imgs/icons/Images";
import "./KnowledgeMQ.css";
import ProjectMqA from '../../ProjectsP/projectMqP/ProjectMqA'



const techStack = [
    {
        img: Images.IconAjax,
        alt: "cone do Ajax",
        text: "AJAX (Asynchronous JavaScript and XML)  uma tcnica que permite que pginas da web faam requisies assncronas ao servidor, sem precisar recarregar a pgina. Isso permite, por exemplo, carregar novos dados dinamicamente, como em feeds ou formulrios interativos."
    },
    {
        img: Images.IconBootstrap,
        alt: "cone do Bootstrap",
        text: "Bootstrap  um framework CSS desenvolvido pelo Twitter que facilita a criao de layouts responsivos com uma coleo de componentes pr-prontos (botes, grids, navbars, modais etc). Ele usa um sistema de grid baseado em colunas e pode ser customizado com Sass."
    },
    {
        img: Images.IconCSS3,
        alt: "cone do CSS3",
        text: "CSS3  a tecnologia usada para estilizar pginas HTML. A terceira verso trouxe avanos como animaes, transies, gradientes, flexbox, grid layout e media queries, permitindo designs mais ricos, responsivos e interativos sem depender de JavaScript."
    },
    {
        img: Images.IconGulp,
        alt: "cone do Gulp",
        text: "Gulp  um automatizador de tarefas para projetos front-end. Ele permite compilar arquivos Sass, minificar CSS/JS, otimizar imagens, e muito mais. Usa um sistema de streams para processar arquivos rapidamente, sendo til em pipelines de build."
    },
    {
        img: Images.IconHTML5,
        alt: "cone do HTML5",
        text: "HTML5  a verso mais recente da linguagem de marcao usada para estruturar pginas da web. Ele introduziu novas tags semnticas, suporte para udio, vdeo e grficos via canvas e svg, formulrios mais inteligentes, e maior integrao com APIs."
    },
    {
        img: Images.IconJQUERY,
        alt: "cone do jQuery",
        text: "jQuery  uma biblioteca JavaScript criada para simplificar a manipulao do DOM, eventos e requisies AJAX. Antes dos frameworks modernos, era amplamente usada por facilitar tarefas repetitivas com uma sintaxe simples e compatibilidade entre navegadores."
    },
    {
        img: Images.IconJS,
        alt: "cone do JavaScript",
        text: "JavaScript  uma linguagem de programao essencial para o desenvolvimento web. Ele permite criar pginas interativas, manipular o DOM, responder a eventos do usurio, e tambm pode ser usado no backend com Node.js.  a base de frameworks modernos como React, Vue e Angular."
    },
    {
        img: Images.IconREACT,
        alt: "cone do React",
        text: "React  uma biblioteca JavaScript desenvolvida pelo Facebook para criar interfaces de usurio baseadas em componentes reutilizveis. Ele  conhecido por sua performance, renderizao eficiente com o virtual DOM e por facilitar o desenvolvimento de aplicaes web modernas de pgina nica (SPAs)."
    },
    {
        img: Images.IconWEBPACK,
        alt: "cone do Webpack",
        text: "Webpack  um empacotador de mdulos JavaScript que agrupa todos os arquivos (JS, CSS, imagens, etc.) em bundles otimizados para produo. Ele tambm permite usar loaders (como Babel ou Sass) e plugins para transformar e organizar seus recursos web."
    }
];

export default function KnowledgeMQ() {
    const [pageVisible, setPageVisivible] = useState('knowledgemq')
    const variants = {
        visible: {
            x: 0,
            opacity: 1,
            transition: {
                duration: 0.5
            }
        },
        invisibleRight: {
            x: "100%",
            opacity: 0,
            transition: {
                duration: 0.5
            }
        },
        invisibleLeft: {
            x: "-100%",
            opacity: 0,
            transition: {
                duration: 0.5
            }
        }
    }
    return (
        <div
            className="project-knowledgemq"
            data-aos="fade-right"
            data-aos-delay="300"
            data-aos-duration="3000"
        >
            <motion.div
                drag="x"
                dragSnapToOrigin={true}
                onDragEnd={(event, info) => {
                    const swipeThreshold = 50
                    if (info.offset.x < -swipeThreshold) {
                        setPageVisivible('projectmq')
                    }
                }}
                variants={variants}
                animate={pageVisible === 'knowledgemq' ? 'visible' : 'invisibleLeft'}
            >
                <div className="knowledgemq">
                    <h1 className="display-6 language-KMQ">
                        <Typewriter
                            words={["Tech Stack."]}
                            cursor
                            cursorStyle="_"
                            typeSpeed={150}
                        />
                    </h1>
                    <div className="swipe-indicator-right">
                        <motion.div
                            animate={{ x: [0, 5, 0] }}
                            transition={{ repeat: Infinity, duration: 1 }}
                        >
                            Projetos <i class="bi bi-chevron-double-right"></i>
                        </motion.div>
                    </div>
                    {techStack.map((tech, index) => (
                        <motion.div
                            key={index}
                            className="img-text"
                            initial={{ opacity: 0, y: 30 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            transition={{ duration: 0.5, delay: index * 0.1 }}
                            viewport={{ once: true }}
                        >
                            <img src={tech.img} className="img" alt={tech.alt} />
                            <p>{tech.text}</p>
                        </motion.div>
                    ))}
                </div>
            </motion.div>
            <motion.div
                drag="x"
                dragSnapToOrigin={true}
                onDragEnd={(event, info) => {
                    const swipeThreshold = 50
                    if (info.offset.x > swipeThreshold) {
                        setPageVisivible('knowledgemq')
                    }
                }}
                variants={variants}
                animate={pageVisible === 'projectmq' ? 'visible' : 'invisibleRight'}
                className="projectmq"
            >
                <ProjectMqA />
            </motion.div>
        </div>
    );
}
